"use client";

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import Card from '../../../components/Card';
import Button from '../../../components/Button';
import FormField from '../../../components/FormField';
import Alert from '../../../components/Alert';
import Spinner from '../../../components/Spinner';
import Input from '../../../components/Input';

export default function DoctorLogin() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [fieldErrors, setFieldErrors] = useState<{ email?: string; password?: string }>({});
  const [loading, setLoading] = useState(false);
  const router = useRouter();

  const validate = () => {
    const errs: { email?: string; password?: string } = {};
    if (!email || !email.includes('@')) errs.email = 'Please enter a valid email';
    if (!password || password.length < 6) errs.password = 'Password must be at least 6 characters';
    setFieldErrors(errs);
    return Object.keys(errs).length ? errs : null;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    const clientError = validate();
    if (clientError) {
      const first = Object.values(clientError as any)[0] as string;
      return setError(first);
    }
    setError('');
    setLoading(true);

    try {
      const response = await fetch('/api/auth/doctor/login', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email, password }),
      });

      const data = await response.json();

      if (!response.ok) {
        setError(data.error || 'Login failed');
        return;
      }

      localStorage.setItem('token', data.token);
      localStorage.setItem('user', JSON.stringify(data.doctor));
      localStorage.setItem('userRole', 'doctor');

      router.push('/doctor/dashboard');
    } catch (err: any) {
      setError(err.message || 'An error occurred');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="max-w-md mx-auto mt-12">
      <Card>
        <h1 className="text-2xl font-bold mb-4">Doctor Login</h1>

        {error && <Alert type="error">{error}</Alert>}

        <form onSubmit={handleSubmit} className="mt-4">
          <FormField label="Email" htmlFor="email">
            <Input
              id="email"
              type="email"
              value={email}
              onChange={(e) => { setEmail(e.target.value); setFieldErrors((s) => ({ ...s, email: undefined })); }}
              className={fieldErrors.email ? 'border-red-400 ring-1 ring-red-100' : ''}
            />
          </FormField>

          <FormField label="Password" htmlFor="password">
            <Input
              id="password"
              type="password"
              value={password}
              onChange={(e) => { setPassword(e.target.value); setFieldErrors((s) => ({ ...s, password: undefined })); }}
              className={fieldErrors.password ? 'border-red-400 ring-1 ring-red-100' : ''}
            />
          </FormField>

          <div className="mt-4">
            <Button type="submit" className="w-full" disabled={loading}>
              {loading ? <Spinner className="h-4 w-4" /> : 'Login'}
            </Button>
          </div>
        </form>

        <p className="mt-4 text-center text-sm text-gray-600">Don't have an account? <Link href="/doctor/register" className="text-blue-600 underline">Register here</Link></p>

        <p className="mt-2 text-center text-sm"><Link href="/login" className="text-blue-600 underline">Patient login</Link></p>
      </Card>
    </div>
  );
}
